.chat {
  display: flex;
  flex-direction: column;
  height: 60%;
  max-width: 620px;
  width: 100%;
  border-radius: 5px;
  background-color: #0c162d;
  box-shadow: 0 0 0 1px rgb(255 255 255 / 10%), 0 34px 65px #040d21, 0 2.75px 2.21px rgb(0 0 0 / 7%), 0 6.65px 5.32px rgb(0 0 0 / 4%), 0 12.5px 10px rgb(0 0 0 / 3%), 0 22px 18px rgb(0 0 0 / 3%), 0 42px 33.4px rgb(0 0 0 / 2%), 0 100px 80px rgb(0 0 0 / 2%);

  @media (max-width: 768px) {
    max-width: none;
    height: 100%;
  }

  &__header {
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 8px;
    padding: 10px 15px;
    color: #b5bfd2;
    border-bottom: 1px solid #040d21;

    &-name {
      line-height: 1.6em;
    }

    &-photo {
      width: 40px;
      height: 40px;
    }

    &-status {
      position: relative;
      width: 6px;
      height: 6px;
      background-color: rgb(38, 190, 38);
      border-radius: 50%;
      cursor: pointer;

      &:hover {
        &:after {
          content: attr(data-tooltip);
          padding: 2px 4px;
          position: absolute;
          bottom: calc(100% + 5px);  /* At the top of the tooltip */
          left: 50%;
          transform: translateX(-50%);
          background-color: rgb(58, 52, 52);
          font-size: 0.8rem;
          border-radius: 4px;
          // opacity: 0;
          // visibility: hidden;
          transition: all 0.3s ease;
        }
      }
    }

    &-typing {
      position: absolute;
      bottom: 4px;
      left: 63px;
      font-size: 0.75rem;
      color: #6c98cb;
    }
  }

  &__messages-box {
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
    padding: 15px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    flex-direction: column;
    row-gap: 13px;
    overflow: auto;
    min-height: 200px;
    scrollbar-width: thin;          /* "auto" or "thin" */
    scrollbar-color: #041b30 transparent;   /* scroll thumb and track */

    &::-webkit-scrollbar {
      width: 5px;               /* width of the entire scrollbar */
    }
    
    &::-webkit-scrollbar-track {
      background: transparent;        /* color of the tracking area */
    }
    
    &::-webkit-scrollbar-thumb {
      background-color: rgba(0, 0, 0, 0.5);    /* color of the scroll thumb */
      border-radius: 20px;       /* roundness of the scroll thumb */
    }
  }

  &__message {
    display: flex;
    flex-direction: column;
    row-gap: 8px;
    max-width: 70%;

    &-header {
      color: #b5bfd2;
    }

    &-text {
      position: relative;
      border-radius: 5px;
      padding: 5px 8px;
      word-wrap: break-word;
      white-space: pre-line;

      &--command {
        color: #8fb5fb !important;
      }
    }

    &--recipient {
      margin-right: auto;

      .chat__message-text {
        margin-right: auto;
        $bgColor: rgb(98, 117, 151);
        background-color: $bgColor;
        color: darken($bgColor, 50%);
      }
    }

    &--sender {
      margin-left: auto;

      .chat__message-header {
        margin-left: auto;
      }

      .chat__message-text {
        margin-left: auto;
        $bgColor: rgba(56,139,253, 0.5);
        background-color: $bgColor;
        color: rgba(#fff, 0.9);
      }
    }
  }

  &__controls {
    border-top: 1px solid #040d21;
    position: relative;
    padding: 10px;
    margin-top: auto;
    display: flex;
    background-color: rgba(#040d21, 0.7);

    &--disabled {
      opacity: 0.55;
    }
  }

  &__input {
    background-color: transparent;
    border: none;
    box-sizing: border-box;
    font-size: 1rem;
    color: rgba(white, 0.9);
    width: 100%;
    outline: none;
  }

  &__submit {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
    background-color: transparent;
    border: none;
    width: 18px;
    height: 18px;
    background-image: url(../../assets/images/send-message.png);
    background-size: 100%;
    background-repeat: no-repeat;
    outline: none;

    &:hover {
      &:after {
        content: attr(data-tooltip);
        color: white;
        padding: 2px 4px;
        position: absolute;
        bottom: calc(100% + 5px);  /* At the top of the tooltip */
        left: 50%;
        transform: translateX(-50%);
        background-color: rgb(58, 52, 52);
        font-size: 0.8rem;
        border-radius: 4px;
        // opacity: 0;
        // visibility: hidden;
        transition: all 0.3s ease;
        z-index: 1000;
      }
    }
  }
}